import React, { Component } from "react"
import MetaTags from 'react-meta-tags';

import { format } from 'date-fns';
import {
  Card,
  Col,
  Container,
  Row,
  CardBody,
  CardHeader,
  CardTitle,
  FormGroup,
  Label,
  Button,
  Form,
  Input,
  InputGroupAddon,
  InputGroupText,
  InputGroup,
  Breadcrumb,
  BreadcrumbItem,
 
  

} from "reactstrap"
import Select from "react-select"
import axios from 'axios'
import  { Redirect } from 'react-router-dom'
// import $ from "jquery";

import Flatpickr from "react-flatpickr"
import ReactSelect from 'react-select'
import makeAnimated from 'react-select/animated';
//import BootstrapSelect from "bootstrap-select"
import url from "../../common/api"

class DetailEvenement extends Component {

  constructor(props) {
    super(props)

    this.state = { ok:"",evenement:{},systeme:[],unite:[],langue:[],thematique:[],mot:[],video:"",image:[],pjointe:[],localite:[]   }
  }

  componentDidMount(){
      //   const requestOptions = {
  //     method: 'GET',
  //     headers: { 'Content-Type': 'application/json','Accept': 'application/json' },
  //     body: JSON.stringify(data)
  // };
    var id=localStorage.getItem("idressource")
    fetch(url+"evenements/"+id)
    //fetch("http://localhost:3000/evenements/"+id)
    .then((response) => response.json())
    .then((data) => {
        this.setState({evenement:data})
        console.log("la ressource:",data)
        this.setState({systeme:data.type_systeme_irrigues})
        this.setState({unite:data.unite_administratives})
        this.setState({thematique:data.thematiques})
      

      

        
        $("#libelle").val(data.libelle)
        $("#url").val(data.url)
        $("#description").val(data.description)
        $("#lieu").val(data.lieu)
        $("#objectif").val(data.objectif)
        $("#cible").val(data.cible)
        $("#organisateur").val(data.organisateur)
        $("#date_debut").val(data.date_debut)
        $("#date_fin").val(data.date_fin)

     
        try{
            $("#type_evenement").val(data.type_evenement.libelle)
           }catch(e){
   
           }
        
       
      })
  }
  handleRetour=()=>{
    this.setState({ok: <Redirect to='/liste-evenement'  />});
  }

 
 render() {

  const animatedComponents = makeAnimated();

  const dataTest = [
    {
      value: 1, 
      label:"Test 1"
    },
    {
      value:2, 
      label:"Test 2"
    },
    {
      value:3, 
      label:"Test 3"
    }
  ];

  return (
          <React.Fragment>

            <div className="page-content"> {this.state.ok}

            <MetaTags>
                <title>Détails de l'évenement</title>
            </MetaTags>
            <Container fluid={true}>
                <Row>
                  <Col md="8">
                      <div className="page-title-box d-flex align-items-center justify-content-between">
                      <h4 className="mb-0 font-size-18">Détails de l'évenement</h4>
                      </div>
                  </Col>
                </Row>
     
                <hr/>

                <Card>
                  <CardBody>
                  <Row> 
                    <Col md="8">
                      <label>Libellé</label>
                      <Input
                            className="form-control"
                            type="text"
                            id="libelle"
                            readOnly
                        />
                    </Col>

                    <Col md="4">
                      <label>Organisateur</label> 
                      <Input
                        className="form-control"
                        type="text"
                        id="organisateur"
                        readOnly
                      />
                    </Col>
                  </Row>
                  
                  <Row className="mt-4"> 
                    <Col md="12" > 
                      <label>Description</label> 
                      <textarea  className="form-control" id="description" readOnly/>
                    </Col>
                  </Row>

                  <Row className="mt-4"> 
                      

                    <Col md="4">

                      <label>Objectif</label>
                      <Input
                        className="form-control"
                        type="text"
                        id="objectif"
                        readOnly
                      />
                    </Col>

                    <Col md="4">

                      <label>Cible</label>
                      <Input
                            className="form-control"
                            type="text"
                            id="cible"
                            readOnly
                        /> 
                    </Col>

                    <Col md="4">
                      <label>Lieu</label>
                      <Input
                            className="form-control"
                            type="text"
                            id="lieu"
                            readOnly
                        /> 
                    </Col>

                    
                  </Row>

                  

                  

                  <Row className="mt-4"> 
                      <Col md="4">
                        <label>Types D'évenement</label>
                        <Input
                            className="form-control"
                            type="text"
                            id="type_evenement"
                            readOnly
                        />
                      </Col>

                      <Col md="4">
                        <label>Date de début</label>
                        <Input
                            className="form-control"
                            type="text"
                            id="date_debut"
                            readOnly
                        />
                      </Col>


                      <Col md="4">
                        <label>Date de fin</label>
                        <Input
                            className="form-control"
                            type="text"
                            id="date_fin"
                            readOnly
                        />
                      </Col>
                  </Row>

                  <Row className="mt-4"> 
                    <Col md="12"> 
                      <label>Url</label>
                        <Input
                            className="form-control"
                            type="text"
                            id="url"
                            readOnly
                        /> 
                    </Col>
                  </Row>
                  </CardBody>
                </Card>


                <Row className="mb-4">
                    <Col md="6">
                        <Card>
                        <CardBody>
                            <CardTitle className="mb-4 h4">Type(s) de système(s) irrigué(s)</CardTitle>

                            <div className=" mt-4">

                            <table className="table align-middle">
                                <thead>
                                    <tr>
                                        <th>Libelle</th> <th>Sigle</th>
                                    </tr>
                                </thead>
                                <tbody>


                                    {
                                        (this.state.systeme.length!=0)?

                                        (this.state.systeme.map(
                                            (element,idx)=>(
                                                <tr>
                                                <td style={{ width: "30%" }}>
                                                    <p className="mb-0">{element.libelle}</p>
                                                </td>
                                                <td style={{ width: "30%" }}>
                                                    <p className="mb-0">{element.sigle}</p>
                                                </td>
                                            </tr>
                                            )

                                        )):(

                                            <tr>
                                            <td style={{ width: "30%" }}>
                                                <p className="mb-0">Néant</p>
                                            </td>
                                            <td style={{ width: "30%" }}>
                                                <p className="mb-0">Néant</p>
                                            </td>
                                        </tr>
                                        )

                                    }                              


                                </tbody>
                            </table>
                            </div>
                        </CardBody>
                        </Card>
                    </Col>
                    
                    <Col md="6">
                        <Card>
                        <CardBody>
                            <CardTitle className="mb-4 h4">Unité(s) administrative(s)</CardTitle>

                            <div className=" mt-4">
                            <table className="table align-middle">

                            <thead>

                                    <tr>
                                        <th>Libelle</th> <th>Code</th> 
                                    </tr>
                                </thead>
                                <tbody>

                                    
                                {
                                (this.state.unite !=0) ?
                                (this.state.unite.map(
                                    (element,idx)=>(
                                        <tr>
                                        <td style={{ width: "30%" }}>
                                            <p className="mb-0">{element.libelle}</p>
                                        </td>
                                        <td style={{ width: "30%" }}>
                                            <p className="mb-0">{element.code}</p>
                                        </td>
                                    </tr>
                                    ))):(

                                        <tr>
                                        <td style={{ width: "30%" }}>
                                            <p className="mb-0">Néant</p>
                                        </td>
                                        <td style={{ width: "30%" }}>
                                            <p className="mb-0">Néant</p>
                                        </td>
                                    </tr>
                                    )

                                

                                } 


                                </tbody>
                            </table>
                            </div>
                            </CardBody>
                        </Card>
                    </Col>
                </Row>

                <Row>
                <Col md="6">
                        <Card>
                        <CardBody>
                            <CardTitle className="mb-4 h4">Thématique(s)</CardTitle>

                            <div className=" mt-4">
                            <table className="table align-middle">

                            {/* <thead>
                                    <tr>
                                        <th>Libelle</th>
                                    </tr>
                                </thead> */}
                                <tbody>
                                    
                                    {
                                    (this.state.thematique.length!=0)?
                                    (this.state.thematique.map(
                                        (element,idx)=>(
                                            <tr>
                                            <td style={{ width: "30%" }}>
                                                <p className="mb-0">{element.libelle}</p>
                                            </td>
                                            
                                        </tr>
                                        )
                                       )
                                    ):(  <tr>
                                        <td style={{ width: "30%" }}>
                                            <p className="mb-0">La liste est vide</p>
                                        </td>
                                        
                                    </tr>)

                                    } 


                                </tbody>
                            </table>
                            </div>
                            </CardBody>
                        </Card>
                    </Col>
                    

                    {/* <Col md="6">

                        <Card>
                        <CardBody>
                            <CardTitle className="mb-4 h4">Localisation(s)</CardTitle>
                        <div className=" mt-4">
                            <table className="table align-middle">
                                <thead>
                                    <tr>
                                        <th>Code de la localité</th> <th>Nom de la localité</th>
                                    </tr>
                                </thead>
                                <tbody>
                                </tbody>
                            </table>
                            </div>
                        </CardBody>
                        </Card>

                    </Col> */}
                </Row>

                <hr></hr>
                <Row>
                <Col md="3">
                    <Button onClick={this.handleRetour} color="secondary" className="b-lg">
                      Retour
                    </Button>
                  </Col>

                </Row>

                
              </Container>
              </div>
          </React.Fragment>
        )}
  }
  export default DetailEvenement