import React, { Component } from "react"
import MetaTags from 'react-meta-tags';

import { format } from 'date-fns';
import {
  Card,
  Col,
  Container,
  Row,
  CardBody,
  CardHeader,
  CardTitle,
  FormGroup,
  Label,
  Button,
  Form,
  Input,
  InputGroupAddon,
  InputGroupText,
  InputGroup,
  Breadcrumb,
  BreadcrumbItem,
 
  

} from "reactstrap"
import Select from "react-select"
import axios from 'axios'
import  { Redirect } from 'react-router-dom'
// import $ from "jquery";

import Flatpickr from "react-flatpickr"
import ReactSelect from 'react-select'
import makeAnimated from 'react-select/animated';
//import BootstrapSelect from "bootstrap-select"
import url from "../../common/api"
class AjoutEvenement extends Component {

  constructor(props) {
    super(props)
    this.state = {
      ok:"",
      thematique:[],
      irrigue:[],
      unite:[],
      public_id:0,
      profile:[],
      type_evenement:[],
      unites_attributes:[],
      systeme_attributes:[],
      profiles_attributes:[],
      thematiques_attributes:[]

    }

    this.handleChangeUnite.bind(this);
    this.handleChangeThematique.bind(this);
    this.handleChangeSysteme.bind(this);
    this.handleChangeThematique.bind(this);
    this.handleDateDebut.bind(this);
    this.handleDateFin.bind(this);
  }

  componentDidMount() {

    $("#chargement").css("display","none")

   $("#profile").css("display","none")
    var date=new Date();
    const NewDate= format(date,'dd/MM/yyyy');
    this.setState({dateDebut:NewDate,dateFin:NewDate})

   fetch(url+"thematiques")
   .then((response) => response.json())
   .then((data) => {
     data.map((element,idx) =>{
       this.setState({thematique:[...this.state.thematique,{value:element.id,label:element.libelle}]})
     })
    
    
    }
    
   );

   fetch(url+"profiles")
   .then((response) => response.json())
   .then((data) => {
     
     data.map((element,idx) =>{
       if(element.libelle=="Public"){
         this.setState({public_id:element.id})

         
       }
       this.setState({profile:data})
     })

    // $("#"+this.state.idPrive+data[0].id).prop('checked',true)
  
    }
    
   );
  fetch(url+"unite_administratives")
   .then((response) => response.json())
   .then((data) => {
     data.map((element,idx) =>{
       this.setState({unite:[...this.state.unite,{value:element.id,label:element.libelle}]})
     })
     
     
    }
    
   );

   fetch(url+"type_evenements")
   .then((response) => response.json())
   .then((data) => {

     
     this.setState({type_evenement:data})
    }
    
   );

  fetch(url+"type_systeme_irrigues")
  .then((response) => response.json())
   .then((data) => {
     data.map((element,idx) =>{
       this.setState({irrigue:[...this.state.irrigue,{value:element.id,label:element.libelle}]})
     })
     
     
    }
    
   );




 }


 handleChangeThematique = (e) => {
  var data=[]
  e.map((element,idx) =>{
    this.setState({thematiques_attributes:[...data,{"thematique_id":element.value}]})
  })
}
handleChangeUnite = (e) => {
  var data=[]
  e.map((element,idx) =>{
    this.setState({unites_attributes:[...data,{"unite_administrative_id":element.value}]})
  })
}
handleChangeSysteme = (e) => {
  var data=[]
  e.map((element,idx) =>{
    this.setState({systeme_attributes:[...data,{"type_systeme_irrigue_id":element.value}]})
  })
}


handleChangeAcces = () => {
  console.log("le select",$("#acces option:selected").val())
 if($("#acces option:selected").val()=="Privée"){
    $("#profile").css("display","block")
 }else{
  $("#profile").css("display","none")
  this.setState({profiles_attributes:[{"profile_id":this.state.public_id}]})
 }
}
handleChangeProfile = () => {

  this.setState({profiles_attributes:[{"profile_id":$("#profile_id option:selected").val()}]})

}



handleDateDebut= (date)=> {
  const NewDate= format(date[0],'dd/MM/yyyy');
  this.setState({dateDebut:NewDate})
}
handleDateFin= (date)=> {
  const NewDate= format(date[0],'dd/MM/yyyy');
  this.setState({dateFin:NewDate})
}
    
handleSendEvent=()=>{
  
  var date=new Date();
  const NewDate= format(date,'dd/MM/yyyy');
  var data= {
    "libelle": $("#libelle").val(),
    "date_debut": this.state.dateDebut,
    "date_fin":this.state.dateFin,
    "heure_debut": this.state.dateDebut,
    "heure_fin":this.state.dateFin,
    "description": $("#description").val(),
    "url": $("#url").val(),
    "organisateur":$("#organisateur").val(),
    "lieu":$("#lieu").val(),
    "objectif":$("#objectif").val(),
    "cible":$("#cible").val(),
    "type_evenement_id":$("#type_evenement_id option:selected").val(),
    "evenement_profiles_attributes":this.state.profiles_attributes,
    "evenement_thematiques_attributes":this.state.thematiques_attributes,
    "evenement_unite_administratives_attributes":this.state.unites_attributes,
    "evenement_systeme_irrigues_attributes":this.state.systeme_attributes

  }
  const requestOptions = {
    method: 'POST',
    headers: { 'Content-Type': 'application/json','Accept': 'application/json' },
    body: JSON.stringify(data)
};
  


fetch(url+'evenements', requestOptions)
   .then(response => response.json()
  )
  .then(data =>{console.log("enregitre avec succes vrai:",data)
    $("#chargement").css("display","none")
    this.handleRetour()
 
 } )
}

handleRetour=()=>{
  this.setState({ok: <Redirect to='/liste-evenement'  />});
}


 
 render() {

  const animatedComponents = makeAnimated();



  return (
          <React.Fragment>
            <div className="page-content">   {this.state.ok}

            <MetaTags>
                <title>Créer un évenement</title>
            </MetaTags>
            <Container fluid={true}>
                <Row>
                  <Col md="8">
                      <div className="page-title-box d-flex align-items-center justify-content-between">
                      <h4 className="mb-0 font-size-18">Créer un évenement</h4>
                      </div>
                  </Col>
                </Row>
     
                <hr/>

                <Card>
                  <CardBody>
                  <Row> 
                    <Col md="8">
                      <label>Libellé</label>
                      <Input
                            className="form-control"
                            type="text"
                            id="libelle"
                            placeholder="Libellé de l'évenement"
                        />
                    </Col>

                    <Col md="4">
                      <label>Organisateur</label> 
                      <Input
                        className="form-control"
                        type="text"
                        id="organisateur"
                        placeholder="Organisateur"
                      />
                    </Col>
                  </Row>
                  
                  <Row className="mt-4"> 
                    <Col md="12" > 
                      <label>Description</label> 
                      <textarea  className="form-control" id="description" placeholder="Description" />
                    </Col>
                  </Row>

                  <Row className="mt-4"> 
                      
                    <Col md="6">
                      <label>Objectif</label>
                      <Input
                        className="form-control"
                        type="text"
                        id="objectif"
                        placeholder="Objectif"
                      />
                    </Col>
                    <Col md="6">
                      <label>Cible</label>
                        <Input
                            className="form-control"
                            type="text"
                            id="cible"
                            placeholder="La cible"
                        /> 
                    </Col>
                    
                  </Row>

                  <Row className="mt-4"> 
                    <Col md="6">
                      <label>Unité(s) administrative(s)</label>
                      <ReactSelect
                        closeMenuOnSelect={false}
                        components={animatedComponents}
                        isMulti

                        onChange={this.handleChangeUnite}
                        options={this.state.unite}

                        placeholder="Choisir l'unité administrative"
                      />
                    </Col>  
                    <Col md="6">
                      <label>Type(s) de système(s) d'irrigation</label>
                      <ReactSelect
                        closeMenuOnSelect={false}
                        components={animatedComponents}
                        isMulti

                         onChange={this.handleChangeSysteme}
                         options={this.state.irrigue}

                        placeholder="Choisir le/les type(s) de système d'irrigation"
                      />
                    </Col>
                  </Row>

                  <Row className="mt-4"> 
                    <Col md="6">
                      <label>Thématique(s)</label>
                      <ReactSelect
                        closeMenuOnSelect={false}
                        components={animatedComponents}
                        isMulti

                         onChange={this.handleChangeThematique}
                         options={this.state.thematique}

                        placeholder="Choisir la/les thématique(s)"
                      />
                    </Col>
                    <Col md="6">
                      <label>Lieu</label>

                      <Input
                            className="form-control"
                            type="text"
                            id="lieu"
                            placeholder="Le lieu"
                        /> 

                    </Col>
                  </Row>

                  <Row className="mt-4"> 
                      <Col md="4">
                        <label>Types D'évenement</label>
                        <select className="form-select" aria-label="Default select example" id="type_evenement_id">
                        <option selected>Choisir le type d'évenement</option>

                        {
                          this.state.type_evenement.map((element,idx) =>(
                            <option value={element.id}>{element.libelle}</option>
                          ))
                        }

                      </select>
                      </Col>

                      <Col md="4">
                        <label>Date et heure de début de l'évenement</label>
                        <div className="docs-datepicker">
                          <InputGroup>
                            <Flatpickr

                              value={this.state.defaultDate}

                              className="form-control d-block"
                              placeholder="Date et heure de début de l'évenement"
                              options={{
                                enableTime: true,
                                altInput: true,
                                // altFormat: "F j, Y",

                                altFormat: "d/M/Y H:i"
                              }}
                               onChange={this.handleDateDebut}

                            />
                            <div className="input-group-append">
                              <button
                                type="button"
                                className="btn btn-outline-secondary docs-datepicker-trigger"
                                disabled
                              >
                                <i
                                  className="fa fa-calendar"
                                  aria-hidden="true"
                                />
                              </button>
                            </div>
                          </InputGroup>
                          <div className="docs-datepicker-container" />
                        </div>
                      </Col>


                      <Col md="4">
                        <label>Date et heure de fin de l'évenement</label>
                        <div className="docs-datepicker">
                          <InputGroup>
                            <Flatpickr

                              value={this.state.defaultDate}

                              className="form-control d-block"
                              placeholder="Date et heure de fin de l'évenement"
                              options={{
                                locale:"fr",
                                enableTime: true,
                                altInput: true,

                                altFormat: "d/M/Y H:i",
                                //dateFormat: "dd/MM/yyyy"
                              }}
                              onChange={this.handleDateFin}

                            />
                            <div className="input-group-append">
                              <button
                                type="button"
                                className="btn btn-outline-secondary docs-datepicker-trigger"
                                disabled
                              >
                                <i
                                  className="fa fa-calendar"
                                  aria-hidden="true"
                                />
                              </button>
                            </div>
                          </InputGroup>
                          <div className="docs-datepicker-container" />
                        </div>
                      </Col>
                  </Row>

                  <Row className="mt-4"> 
                    <Col md="12"> 
                      <label>Url</label>
                      <div className="input-group">
                        <Input
                          type="text"
                          className="form-control"
                          id="url"
                          placeholder="URL"
                        />
                        <div className="input-group-prepend  d-inline-block">
                          <span
                            className="input-group-text h-20"
                            id="validationTooltipUsernamePrepend"
                          >
                            <i className="bx bx-link"></i>
                          </span>
                        </div>
                      </div> 
                    </Col>
                  </Row>

                    

                  </CardBody>
                </Card>

                <hr></hr>


                <Row className="mt-4"> 
                    <Col md="6">
                      <label>Accès</label>
                      <select className="form-select" onClick={this.handleChangeAcces} aria-label="Default select example" id="acces">
                        <option selected>Choisir le type d'accès</option>
                        <option value="Public">Public</option>
                        <option value="Privée">Privée</option>
                      </select>
                    </Col>  
                    <Col md="6" >
                      <div id="profile">
                      <label>Profile(s)</label>
                      <select className="form-select" onClick={this.handleChangeProfile} aria-label="Default select example" id="profile_id">
                        <option selected>Choisir le type profile</option>
                        {
                          this.state.profile.map((element,idx) =>(
                            <option value={element.id}>{element.libelle}</option>
                          ))

                        }
                      </select>
                    
                      </div>
                    </Col>
                  </Row>
     


                <Row className="justify-content-start mt-5 mb-5">
                    
                  <Col md="6">
                    <Button id="initial" color="info" className="b-lg" 

                     onClick={this.handleSendEvent}
                    >
                      Créer 
                    </Button>
                    <Button id="chargement" color="info" className="b-lg" type="button" disabled>
                    <span className="spinner-border spinner-border-sm" role="status" aria-hidden="true"> </span>
                      Envoi...
                    </Button>

                  </Col>

                  <Col md="2">
                    <Button  color="secondary" className="b-lg">
                      Annuler
                    </Button>
                  </Col>

                </Row>

              </Container>
              </div>
             
          </React.Fragment>
        )}

  }
  export default AjoutEvenement