import { Icon } from "leaflet"
import React, { Component } from "react";
import PropTypes from 'prop-types';
import MetaTags from 'react-meta-tags';
import {
    Container,Row,  Col,  Button,  Card,  CardBody,  CardTitle,  Modal,  ModalHeader,
    ModalBody,  ModalFooter,  Media,  Table,  Input,  ButtonDropdown,  DropdownToggle,
    DropdownMenu,  DropdownItem,  Dropdown, InputGroup, CardFooter, CardHeader} from "reactstrap"

import Select from "react-select"
import Flatpickr from "react-flatpickr"
import Timeline from 'react-time-line'
//Import Breadcrumb
import Breadcrumbs from "../../components/Common/Breadcrumb"
import SplineArea from "../AllCharts/apex/SplineArea"
import ColumnWithDataLabels from "../AllCharts/apex/ColumnWithDataLabels"

import  { Redirect } from 'react-router-dom'

import imageSIS from "../../assets/images/logoSIS.png"

import $ from "jquery";
import url from "../../common/api"

class DetailRessource extends Component {
  constructor(props) {
    super(props)


    this.state = {ok:"", ressource:{},systeme:[],unite:[],langue:[],thematique:[],mot:[],video:"",image:[],pjointe:[],localite:[]   }

  }

  componentDidMount(){

    var id=localStorage.getItem("idressource")

    var taille=0

    fetch(url+"ressources/"+id)
    .then((response) => response.json())
    .then((data) => {
        this.setState({ressource:data})
        console.log("la ressource:",data)
        this.setState({systeme:data.type_systeme_irrigues})
        this.setState({langue:data.langues})
        this.setState({unite:data.unite_administratives})
        this.setState({thematique:data.thematiques})
        this.setState({mot:data.mot_cles})

        this.setState({localite:data.pays})

        
        $("#nom").val(data.nom)
        $("#url").val(data.url)
        $("#description").val(data.description)
        $("#statut").val(data.statut)
        $("#datePublication").val(data.date_debut_publication)
        $("#dateFinPublication").val(data.date_fin_publication)

        if(data.video==""){
          $("#video").css("display","none")
        }


        data.pjointes.map(
            (element,idx)=>{

                taille=parseInt(element.taille)/1024
                this.setState({pjointe:[...this.state.pjointe,{taille:taille+" KO",nom:element.file.split('.')[0].split('-')[1],file:"http://localhost/SIS/fichiers/data/"+element.file,type:element.file.split('.')[1]}]})
                // if(parseInt(element.taille)<1024){
                //   taille=parseInt(element.taille)/1024
                //   this.setState({pjointe:[...this.state.pjointe,{taille:taille+"KO",nom:element.file.split('.')[0].split('-')[1],file:"http://localhost/SIS/fichiers/data/"+element.file,type:element.file.split('.')[1]}]})
                // }
                // if(parseInt(element.taille)>=1024){
                //     taille=parseInt(element.taille)/1024
                //     this.setState({pjointe:[...this.state.pjointe,{taille:taille+"MO",nom:element.file.split('.')[0].split('-')[1],file:"http://localhost/SIS/fichiers/data/"+element.file,type:element.file.split('.')[1]}]})
                //   }
               

               
            }
        )

        data.images.map(
            (element,idx)=>{
                this.setState({image:[...this.state.image,"http://localhost/SIS/fichiers/data/"+element.file]})
            }
        )
        try{
         $("#format").val(data.le_format.libelle)
        }catch(e){

        }
        try{
            $("#type_ressource").val(data.type_ressource.libelle)
           }catch(e){
   
           }
        try{
            $("#format").val(data.format.libelle)
           }catch(e){
   
        }   
        try{
            this.setState({video:data.video.replace("watch?v=","embed/")})
           }catch(e){
   
        }
      })
  }

  handleRetour=()=>{
    this.setState({ok: <Redirect to='/accueil'/>});
  }

 
  render() {

    
    return (
      <React.Fragment>

                            {this.state.ok}

        <div className="page-content mb-3">
            <MetaTags>
                <title>Détails de la ressource</title>
            </MetaTags>
            <div className="container-fluid ">
                <Breadcrumbs title="Détails de la ressource" breadcrumbItem="Détails de la ressource" />

                <Card className="mb-4">
                  <CardBody>
                  <Row> 
                    <Col md="12">
                    <label>Titre de la ressource</label>
                      <Input
                            className="form-control"
                            type="text"
                            id="nom"
                            readOnly
                            // placeholder="Titre de la ressource"
                        />
                    </Col>
                  </Row>


                  <Row className="mt-4"> 
                    <Col md="7">
                    <label>Type de ressource</label> 
                        <Input
                            className="form-control"
                            type="text"


                            id="type_ressource"


                            readOnly
                            // placeholder="Titre de la ressource"
                        />
                    </Col>
                    
                    <Col md="3">
                    <label>Format</label>
                        <Input
                            className="form-control"
                            type="text"


                            id="format"

                            readOnly
                            // placeholder="Titre de la ressource"
                        />
                    </Col>

                    <Col md="2">
                        <label>Statut</label>
                        <Input
                                className="form-control"
                                type="text"
                                id="statut"
                                readOnly
                            />
                    </Col>
                  </Row>


                  <Row className="mt-4"> 
                    <Col md="12">
                    <label>Description de la ressource</label>
                      <textarea  className="form-control" id="description" readOnly />
                            
                    </Col>
                  </Row>

                  <Row className="mt-4">
                    <Col md="12">

                        <label>Visitez le lien suivant:</label> 
                        <p> <a href={this.state.ressource.url}>{this.state.ressource.url}</a></p>
                        {/* <Input

                            className="form-control"
                            type="text"
                            id="url"
                            readOnly
                            // placeholder="Titre de la ressource"


                        /> */}


                    </Col>
                    
                  </Row>

                    <Row className="mt-4">
                        <Col md="6">
                            <label>Date de début de publication</label>
                            <Input
                                className="form-control"
                                type="text"


                                id="datePublication"


                                readOnly
                            />
                        </Col>

                        <Col md="6">
                            <label>Date de fin de publication</label>
                            <Input
                                className="form-control"
                                type="text"
                                id="dateFinPublication"
                                readOnly
                            />
                        </Col>
                    </Row>

                  </CardBody>
                </Card>


                <Row className="mb-4">
                    
                    <Col md="6">
                        <Card>
                        <CardBody>

                        <CardTitle className="mb-4 h4">Type(s) de système(s) irrigué(s)</CardTitle>
                        <div className=" mt-4">
                            <table className="table align-middle">
                                <thead>
                                    <tr>
                                        <th>Libelle</th> <th>Sigle</th>
                                    </tr>
                                </thead>
                                <tbody>

                                    {
                                        (this.state.systeme.length!=0)?

                                        (this.state.systeme.map(
                                            (element,idx)=>(
                                                <tr>
                                                <td style={{ width: "30%" }}>
                                                    <p className="mb-0">{element.libelle}</p>
                                                </td>
                                                <td style={{ width: "30%" }}>
                                                    <p className="mb-0">{element.sigle}</p>
                                                </td>
                                            </tr>
                                            )

                                        )):(

                                            <tr>
                                            <td style={{ width: "30%" }}>
                                                <p className="mb-0">Néant</p>
                                            </td>
                                            <td style={{ width: "30%" }}>
                                                <p className="mb-0">Néant</p>
                                            </td>
                                        </tr>
                                        )

                                    }                              

                                </tbody>
                            </table>
                            </div>
                        </CardBody>
                        </Card>
                    </Col>
                    
                    <Col md="6">
                        <Card>
                        <CardBody>

                        <CardTitle className="mb-4 h4">Unité(s) administrative(s)</CardTitle>
                        <div className=" mt-4">
                            <table className="table align-middle">

                            <thead>
                                    <tr>
                                        <th>Libelle</th> <th>Code</th> 
                                    </tr>
                                </thead>
                                <tbody>
                                    
                                {
                                (this.state.unite !=0) ?
                                (this.state.unite.map(
                                    (element,idx)=>(
                                        <tr>
                                        <td style={{ width: "30%" }}>
                                            <p className="mb-0">{element.libelle}</p>
                                        </td>
                                        <td style={{ width: "30%" }}>
                                            <p className="mb-0">{element.code}</p>
                                        </td>
                                    </tr>
                                    ))):(

                                        <tr>
                                        <td style={{ width: "30%" }}>
                                            <p className="mb-0">Néant</p>
                                        </td>
                                        <td style={{ width: "30%" }}>
                                            <p className="mb-0">Néant</p>
                                        </td>
                                    </tr>
                                    )

                                

                                } 

                                </tbody>
                            </table>
                            </div>
                            </CardBody>
                        </Card>
                    </Col>
                    
                    
                </Row>

                <Row>
                <Col md="6">
                        <Card>
                        <CardBody>

                        <CardTitle className="mb-4 h4">Thématique(s)</CardTitle>
                        <div className=" mt-4">
                            <table className="table align-middle">

                            {/* <thead>
                                    <tr>
                                        <th>Libelle</th>
                                    </tr>
                                </thead> */}
                                <tbody>
                                    
                                    {
                                    (this.state.thematique.length!=0)?
                                    (this.state.thematique.map(
                                        (element,idx)=>(
                                            <tr>
                                            <td style={{ width: "30%" }}>
                                                <p className="mb-0">{element.libelle}</p>
                                            </td>
                                            
                                        </tr>
                                        )
                                       )
                                    ):(  <tr>
                                        <td style={{ width: "30%" }}>
                                            <p className="mb-0">La liste est vide</p>
                                        </td>
                                        
                                    </tr>)

                                    } 

                                </tbody>
                            </table>
                            </div>
                            </CardBody>
                        </Card>
                    </Col>
                    
                    <Col md="6">
                        <Card>
                        <CardBody>


                        <CardTitle className="mb-4 h4">Localisation(s)</CardTitle>

                        <div className=" mt-4">
                            <table className="table align-middle">
                            <thead>
                                    <tr>

                                        <th>Code de la localité</th> <th>Nom de la localité</th> 

                                    </tr>
                                </thead>
                                <tbody>
                                    
                                {
                                (this.state.localite !=0) ?
                                (this.state.localite.map(
                                    (element,idx)=>(
                                        <tr>
                                        <td style={{ width: "30%" }}>

                                            <p className="mb-0">{element.code}</p>

                                        </td>
                                        <td style={{ width: "30%" }}>
                                            <p className="mb-0">{element.nom}</p>
                                        </td>
                                    </tr>
                                    ))):(

                                        <tr>
                                        <td style={{ width: "30%" }}>
                                            <p className="mb-0">Néant</p>
                                        </td>
                                        <td style={{ width: "30%" }}>
                                            <p className="mb-0">Néant</p>
                                        </td>
                                    </tr>
                                    )

                                

                                } 

                                </tbody>
                            </table>
                            </div>
                        </CardBody>
                        </Card>
                    </Col>
                </Row>

                <Row>
                <Col md="6">
                        <Card>
                        <CardBody>

                        <CardTitle className="mb-4 h4">Mot(s) clé(s)</CardTitle>
                        <div className=" mt-4">
                            <table className="table align-middle">

                            <thead>
                                    {/* <tr>
                                        <th>Libelle</th>
                                    </tr> */}
                                </thead>
                                <tbody>
                                  
                                {
                                    (this.state.mot.length!=0)?
                                   
                                    (this.state.mot.map(
                                        (element,idx)=>(
                                            <tr>
                                            <td style={{ width: "30%" }}>
                                                <p className="mb-0">{element.libelle}</p>
                                            </td>
                                          
                                        </tr>
                                        )

                                    )):(  <tr>
                                        <td style={{ width: "30%" }}>
                                            <p className="mb-0">La liste est vide</p>
                                        </td>
                                        
                                    </tr>)

                                    } 

                                </tbody>
                            </table>
                            </div>
                            </CardBody>
                        </Card>
                    </Col>
                    
                    <Col md="6">
                        <Card>
                        <CardBody>

                        <CardTitle className="mb-4 h4">Langue(s)</CardTitle>
                        <div className=" mt-4">
                            <table className="table align-middle">
                                <tbody>
                                
                                {
                                    (this.state.langue.length!=0) ?
                                    (this.state.langue.map(
                                        (element,idx)=>(
                                            <tr>
                                            <td style={{ width: "30%" }}>
                                                <p className="mb-0">{element.nom}</p>
                                            </td>
                                        
                                        </tr>
                                        )

                                    )):(  <tr>
                                        <td style={{ width: "30%" }}>
                                            <p className="mb-0">La liste est vide</p>
                                        </td>
                                        
                                    </tr>)

                                    } 

                                </tbody>
                            </table>
                            </div>
                        </CardBody>
                        </Card>
                    </Col>
                </Row>


                <Row className="mb-4">

                <h5>Image(s)</h5>

                   {

                        (this.state.image.length!=0)?
                        (
                         (this.state.image.map(
                             (element,idx)=>(
                                <Col md="4">
                                <Card>
                                    <img className="card-img-top" src={element} alt="Image"/>
                                    <CardFooter>
                                        <small className="text-muted">Titre Image</small>
                                    </CardFooter>
                                </Card>
                            </Col>
                             )
                         ))
                           

                        ):(<span>pas d'image</span>)

                   }
                    {/* <Col md="4">

                        <Card>
                            <img className="card-img-top" src={imageSIS} alt="Image"/>
                            <CardFooter>
                                <small className="text-muted">Titre Image</small>
                            </CardFooter>
                        </Card>
                    </Col>
                    <Col md="4">
                        <Card>
                            <img className="card-img-top" src={imageSIS} alt="Image"/>
                            <CardFooter>
                                <small className="text-muted">Titre Image</small>
                            </CardFooter>
                        </Card>
                    </Col>
                    <Col md="4">
                        <Card>
                            <img className="card-img-top" src={imageSIS} alt="Image"/>
                            <CardFooter>
                                <small className="text-muted">Titre Image</small>
                            </CardFooter>
                        </Card>


                    </Col> */}

                </Row>

                
                <h5>Vidéo</h5>
                <div className="embed-responsive embed-responsive-16by9 mb-4">

                    <iframe className="embed-responsive-item" src={this.state.video} height="400" width="900" allowfullscreen frameborder="4"></iframe>

                </div>


                <Row className="mt-4 mb-4">
                    <Card>
                        <CardBody>

                            <CardTitle className="mb-4 h4">Pièce(s) jointe</CardTitle>

                            <div className="text-center">
                                <div className="mb-4">
                                    <i className="bx bx-file text-primary display-5" style={{ lineHeight : '1'}}/>
                                </div>
                            </div>

                            <div className="table-responsive mt-4">
                            <table className="table align-middle table-nowrap">
                                <thead>
                                    <tr>
                                        <th style={{ width: "30%" }}>

                                            Taille
                                        </th>
                                        <th style={{ width: "30%" }}>

                                            Nom
                                        </th>
                                        <th style={{ width: "25%" }}>
                                            Type
                                        </th>
                                        <th>
                                            Télécharger
                                        </th>
                                    </tr>
                                </thead>

                                <tbody>

                                    {
                                        (this.state.pjointe.length!=0)?
                                        (
                                           ( 
                                             this.state.pjointe.map(
                                                (element,idx)=>(
                                                    <tr>
                                                    <td style={{ width: "30%" }}>

                                                        <p className="mb-0">{element.taille}</p>
                                                    </td>
                                                    <td style={{ width: "30%" }}>

                                                   <p className="mb-0">{element.nom}</p>
                                                    </td>
                                                    <td style={{ width: "25%" }}>
                                                        <h5 className="mb-0">{element.type}</h5>
                                                    </td>
                                                    <td>
                                                        <a href={element.file} download > <i className="bx bxs-download bx-sm text-primary " /> </a>
                                                    </td>
                                                </tr>
                                                )
                                             )
                                                
                                           )   
                                           

                                        ):null


                                    }
                                    {/* // <tr>
                                    //     <td style={{ width: "30%" }}>
                                    //         <p className="mb-0">Document word</p>
                                    //     </td>
                                    //     <td style={{ width: "25%" }}>
                                    //         <h5 className="mb-0">word</h5>
                                    //     </td>
                                    //     <td>
                                    //         <a><i className="bx bxs-download bx-sm text-primary " /> </a>
                                    //     </td>
                                    // </tr>
                                    // <tr>
                                    //     <td style={{ width: "30%" }}>
                                    //         <p className="mb-0">Document pdf</p>
                                    //     </td>
                                    //     <td style={{ width: "25%" }}>
                                    //         <h5 className="mb-0">pdf</h5>
                                    //     </td>
                                    //     <td>
                                    //         <a><i className="bx bxs-download bx-sm text-primary " /> </a>
                                    //     </td>
                                    // </tr>
                                    // <tr>
                                    //     <td style={{ width: "30%" }}>
                                    //         <p className="mb-0">Document excel</p>
                                    //     </td>
                                    //     <td style={{ width: "25%" }}>
                                    //         <h5 className="mb-0">xlsx</h5>
                                    //     </td>
                                    //     <td>
                                    //         <a><i className="bx bxs-download bx-sm text-primary " /> </a>
                                    //     </td>
                                    // </tr> */}

                                </tbody>
                            </table>
                            </div>
                        </CardBody>
                    </Card>
                </Row>

                <Row>
                <Col md="3">
                    <Button onClick={this.handleRetour} color="secondary" className="b-lg">
                      Retour
                    </Button>
                  </Col>

                </Row>

                
            </div>
            </div>
      </React.Fragment>
    )
  }
}

export default DetailRessource
